@import "../../global.scss";

.topbar{
  width: 100%;
  height: 70px;
  background-color: #252627;
  color:white;
  position:fixed;
  top:0;
  z-index:3;
  transition: all 1s ease;


  .wrapper{
    padding: 10px 30px;
    display: flex;
    align-items:center;
    justify-content:space-between;

    .left{
      .home{
        font-size: 40px;
        font-weight:700;
        text-decoration:none;
        color:inherit;
        @include mobile{
          font-size:20px;
        }
      }
    }
    
    .right{

      .hamburger{
        width:32px;
        height:25px;
        display: flex;
        flex-direction:column;
        justify-content:space-between;
        cursor:pointer;

        span {
          width: 100%;
          height: 3px;
          background-color: white;
          transform-origin: left;
          transition: all 1s ease;
        }
      }
    }
  }

  &.active{
    color: white;

    .hamburger{
      span{

        &:first-child{
          width:95%;
          background-color: white;
          transform: rotate(45deg);
        }
        &:nth-child(2){
          opacity: 0;
        }
        &:last-child{
          width:95%;
          background-color: white;
          transform: rotate(-45deg);
        }
      }
    }
  }
}